import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material';
import {MatBottomSheet} from '@angular/material';

import {InternPasswordUpdateComponent} from '../intern-password-update/intern-password-update.component';

@Component({
  selector: 'app-intern-profile',
  templateUrl: './intern-profile.component.html',
  styleUrls: ['./intern-profile.component.css']
})
export class InternProfileComponent implements OnInit {

  constructor(private dialog: MatDialog, private bottomSheet:MatBottomSheet) { }

  public intern = {
    name:'Sachin Duhan',
    period:'Feb - May 2019',
    phone:9899376095,
    email:'duhan.sachin2610@gmail.com',
    branch:'Mathematics and Computing',
    domain:'Web Development',
    bank:{
      name:'State Bank of India',
      account:718123842134,
      ifsc:'SBIN0010442',
    }
  }
  public officer = {
    name:'Prof Naokant Deo',
    deptt:'MCE',
    phone:8586821051,
    email:'naokatdeo@dce.ac.in'
  }
  panelOpenState = true;

  ngOnInit() {}

  openPassword(): void {
    if(window.innerWidth < 800){
      this.bottomSheet.open(InternPasswordUpdateComponent);
    }else{
      const config = new MatDialogConfig();
      config.disableClose = false;
      config.height = '80%';
      config.width = '40%';
      config.autoFocus = true;
      config.panelClass = 'DecorateModal';
      this.dialog.open(InternPasswordUpdateComponent, config);
    }
  }

}
