import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { IndexComponent } from './component/index/index.component';
import { NotificationComponent } from './component/notification/notification.component';
import { LoginComponent } from './component/login/login.component';
import { RegisterComponent } from './component/register/register.component';
import { DashComponent } from './component/dashboard/dash/dash.component';
import { NotFoundComponent } from './component/not-found/not-found.component';
import { ApplicationComponent } from './component/dashboard/application/application.component';
import { IntersComponent } from './component/dashboard/inters/inters.component';
import { NotifyComponent } from './component/dashboard/notify/notify.component';
import { ReportComponent } from './component/dashboard/report/report.component';
import { OfficerComponent } from './component/dashboard/officer/officer.component';
import { InternDashComponent } from './component/intern-dash/dash/dash.component';
import { InternNotificationComponent } from './component/intern-dash/intern-notification/intern-notification.component';
import { InternProfileComponent } from './component/intern-dash/intern-profile/intern-profile.component';
import { InternSettingsComponent } from './component/intern-dash/intern-settings/intern-settings.component';

const routes: Routes = [
  { path: '', component: IndexComponent },
  { path: 'notification', component: NotificationComponent },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  // admin routes
  {path: 'admin/dashboard', component: DashComponent},
  { path: 'admin/notify', component: NotifyComponent },
  { path: 'admin/application', component: ApplicationComponent },
  { path: 'admin/intern', component: IntersComponent },
  { path: 'admin/report', component: ReportComponent },
  { path: 'admin/officer', component: OfficerComponent },
  // intern routes
  { path: 'usip/intern', component: InternDashComponent },
  { path: 'usip/intern/notification', component: InternNotificationComponent },
  { path: 'usip/intern/profile', component: InternProfileComponent },
  { path: 'usip/intern/settings', component: InternSettingsComponent },

  // specific path via id!!
  { path: 'admin/intern/:id', component: OfficerComponent },
  // not found URL!
  { path: '**', component: NotFoundComponent },
];


@NgModule({
  exports: [RouterModule],
  imports: [
    RouterModule.forRoot(routes)
  ]
})

export class AppRoutingModule { }

