import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';


import { ToastrService } from 'ngx-toastr';

import { LoginService } from '../../service/login.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public loading: Boolean = false;
  constructor(private fb: FormBuilder,
    private _login: LoginService,
    private toast: ToastrService) { }

  ngOnInit() { }
  // tslint:disable-next-line: member-ordering
  loginForm = this.fb.group({
    userName: [''],
    password: ['']
  });

  submit(): void {
    this.loading = !this.loading;

    //console.log(this.loginForm.value);
    const user = this.loginForm.get('userName').value;

    if (user.includes('-')) {
      const admin = user.split('-');
      console.log(admin);
    } else {
      this._login.postData(this.loginForm.value).subscribe(
        res => {
          //console.log(res.token);
          window.localStorage.setItem('token', res.token);
          window.localStorage.setItem('admin', res.admin);
          if (res.admin) {
            window.localStorage.href = '/dashboard';
          } else {
            window.location.href = '/usip/intern';
          }
          this.loading = !this.loading;
        },
        err => {
          this.loading = !this.loading;
          if (err.status === 401) {
            console.log(err);
            this.toast.error('Login credentials not valid', 'Forbidden');
          } else {
            console.log(err);
            this.toast.error('Login credentials not valid', 'Forbidden');
          }
        }
      );
    }
  }

}
